.PlaceCargo {
  margin: 0;
  padding: 0; 
  height: 100%; 
  width: 100%;
}
.container {
  width: 75%;
  height: 150px;
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}
.cancel-button-container {
  width: 150px;
  height: 80px;
  position: absolute;
  top: 85%;
  left: 25%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}
.confirm-button-container {
  width: 150px;
  height: 80px;
  position: absolute;
  top: 85%;
  left: 75%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}
.container-vibration-status p {
  font-size: large;
  margin: 0;
  color: #80808080;
  font-weight: bold;
}
.lane {
  width: 100%;
  height: 50%;
  margin-top: 0.25%;
  margin-bottom: 0.25%;
  border-style: solid;
  background: #80808080;
}
.optimal-position {
  width: 150px;
  height: 90%;
  top: 50%;
  left: 65%;
  position: relative;
  background-color: #FFC300;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}
img {
  width: 130px;
  height: 60px;
  position: relative;
  top: 50%;
  left: 50%;
  display: none;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}
.button {
  height: 100%;
  width: 100%;
  font-size: x-large;
}
.invalidLane {
  background: #FF000088;
}
