.Corner {
  margin: 0;
  padding: 0; 
  height: 100%; 
  width: 100%;
}

.corner-side { 
  width: 50%; 
  height: 100%; 
  float: left;
  position: relative;
}
.corner { 
  width: 100%; 
  height: 50%; 
  float: left;
}
.corner-container-title {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 15%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center
}
.corner-container-text {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center
}
.corner-button-container-top {
  width: 100px;
  height: 100px;
  position: relative;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}
.corner-button-container-bottom {
  width: 100px;
  height: 100px;
  position: relative;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}
.corner-button {
  height: 100%;
  width: 100%;
  font-size: xxx-large;
}
p {
  font-size: x-large;
  margin: 0;
}
.corner-container-title p {
  font-size: xx-large;
  margin: 0;
}
