html, body, #root {
  margin: 0;
  padding: 0; 
  height: 100%; 
  width: 100%;
  overflow: hidden;
  overscroll-behavior-x: none;
  user-select: none; /* supported by Chrome and Opera */
   -webkit-user-select: none; /* Safari */
   -khtml-user-select: none; /* Konqueror HTML */
   -moz-user-select: none; /* Firefox */
   -ms-user-select: none; /* Internet Explorer/Edge */
}

.ComponentSelector {
  width: 100%;
  height: 100%;
}

.container-vibration-status {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 5%;
  left: 90%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center
}

.container-site-title {
  width: 200px;
  height: 100px;
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center
}

.container-links {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 5%;
  left: 7%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
}

.container-site-title p {
  font-size: xx-large;
  margin: 0;
  font-weight: bold;
}

.container-vibration-status p {
  font-size: large;
  margin: 0;
  color: #80808080;
  font-weight: bold;
}
